import React from "react"
//import { StaticQuery, graphql } from "gatsby"

import ContentTile from "../components/ContentTile.js"

const ContentTiles = ({ siteTitle }) => (
    <div className="cm-content-tiles">

        <ContentTile linkURL="https://www.melbournepack.com.au/">
            <StaticQuery
                query={query}
                render={data => (
                    <img src={data.sponsorLogoMelbournePack.publicURL} alt="Melbourne Pack Logo" />
                )}
            />
        </ContentTile>

        <ContentTile linkURL="http://www.32gi.com.au/">
            <StaticQuery
                query={query}
                render={data => (
                    <img src={data.sponsorLogo32Gi.publicURL} alt="32Gi Logo" />
                )}
            />
        </ContentTile>

        <ContentTile linkURL="http://runfuntravel.com/">
            <StaticQuery
                query={query}
                render={data => (
                    <img src={data.sponsorLogoRunFunTravel.publicURL} alt="Run Fun Travel Logo" />
                )}
            />
        </ContentTile>

        <ContentTile linkURL="https://www.newbalance.com.au/">
        <StaticQuery
                query={query}
                render={data => (
                    <img src={data.sponsorLogoNewBalance.publicURL} alt="New Balance Logo" />
                )}
            />
        </ContentTile>

        <ContentTile linkURL="https://eatandrun.com.au/">
            <StaticQuery
                query={query}
                render={data => (
                    <img src={data.sponsorLogoEatAndRun.publicURL} alt="Eat and Run Logo" />
                )}
            />
        </ContentTile>

    </div>
)

export const sponsorLogo = graphql`
  fragment sponsorLogo on File {
    childImageSharp {
      fixed(width: 200) {
        ...GatsbyImageSharpFixed
      }
    }
  }
`

export const query = graphql`
  query {
    sponsorLogoMelbournePack: file(relativePath: { eq: "sponsor-logo-melbourne-pack.png" }) {
      publicURL
    }
    sponsorLogo32Gi: file(relativePath: { eq: "sponsor-logo-32gi.png" }) {
      publicURL
    }
    sponsorLogoEatAndRun: file(relativePath: { eq: "sponsor-logo-eat-and-run.png" }) {
      publicURL
    }
    sponsorLogoRunFunTravel: file(relativePath: { eq: "sponsor-logo-run-fun-travel.png" }) {
      publicURL
    }
    sponsorLogoNewBalance: file(relativePath: { eq: "sponsor-logo-new-balance.svg" }) {
      publicURL
    }
  }
`

export default ContentTiles;
